'use strict';

/**
* Compute the arithmetic mean of a strided array using Welford's algorithm.
*
* @module @stdlib/stats-base-meanwd
*
* @example
* import meanwd from '@stdlib/stats-base-meanwd';
*
* var x = [ 1.0, -2.0, 2.0 ];
*
* var v = meanwd( x.length, x, 1 );
* // returns ~0.3333
*
* @example
* import meanwd from '@stdlib/stats-base-meanwd';
*
* var x = [ 2.0, 1.0, 2.0, -2.0, -2.0, 2.0, 3.0, 4.0 ];
*
* var v = meanwd.ndarray( 4, x, 2, 1 );
* // returns 1.25
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import ndarray from './ndarray.js';


// MAIN //

setReadOnly( main, 'ndarray', ndarray );


// EXPORTS //

export default main;

// exports: { "ndarray": "main.ndarray" }
